"use client";
import React from "react";

interface Player {
  id: string;
  name: string;
  amount: number;
  isWinner?: boolean;
}

interface SlotMachineProps {
  players: Player[];
  currentWinnerId?: string | null;
  isSpinning: boolean;
}

const SlotMachine: React.FC<SlotMachineProps> = ({
  players,
  currentWinnerId,
  isSpinning,
}) => {
  const coinImgPath = "/images/Coin.png";

  return (
    <div className="relative w-full max-w-3xl mx-auto p-4">
      {/* Frame */}
      <div className="relative bg-gradient-to-b from-[#FFD85A] to-[#F7A531] rounded-2xl border-4 border-[#F7A531] shadow-[0_4px_20px_rgba(0,0,0,0.3)] p-4">
        {/* Header */}
        <div className="text-center mb-4">
          <h2 className="text-3xl font-extrabold text-[#4E2A0B] drop-shadow">
            üé∞ MONKEY BANANA WHEEL üé∞
          </h2>
          <p className="text-[#4E2A0B]/80 font-semibold">Spinning for fortune...</p>
        </div>

        {/* Reel container */}
        <div className="relative bg-[#FFFBE6] rounded-xl border-4 border-[#F7A531] overflow-hidden h-64 shadow-inner">
          {/* Scrolling reel */}
          <div
            className={`absolute inset-0 flex flex-col transition-transform duration-[2000ms] ${
              isSpinning ? "animate-[spinReel_2s_linear_infinite]" : ""
            }`}
            style={{
              transform: isSpinning ? "translateY(-10%)" : "translateY(0)",
            }}
          >
            {players.map((player, i) => {
              const isWinner = currentWinnerId === player.id;
              return (
                <div
                  key={player.id}
                  className={`h-16 flex items-center justify-between px-4 border-b border-[#F7A531]/30 ${
                    isWinner
                      ? "bg-gradient-to-r from-[#FFF5C3] to-[#FFD85A] border-2 border-[#FFD85A] shadow-[0_0_25px_rgba(255,216,90,0.7)]"
                      : "bg-transparent"
                  }`}
                >
                  <div className="flex items-center gap-2">
                    <img
                      src={coinImgPath}
                      alt="coin"
                      className="w-6 h-6 drop-shadow"
                    />
                    <span
                      className={`font-bold ${
                        isWinner ? "text-[#4E2A0B]" : "text-[#5B340A]"
                      }`}
                    >
                      {player.name}
                    </span>
                  </div>

                  <div className="flex items-center gap-1">
                    <span
                      className={`font-extrabold ${
                        isWinner ? "text-[#4E2A0B]" : "text-[#5B340A]"
                      }`}
                    >
                      {player.amount}
                    </span>
                    <img
                      src={coinImgPath}
                      alt="coin"
                      className="w-5 h-5 drop-shadow"
                    />
                  </div>
                </div>
              );
            })}
          </div>

          {/* Center indicator line */}
          <div className="absolute left-0 right-0 top-1/2 -translate-y-1/2 z-10">
            <div className="h-1 bg-red-600 shadow-[0_0_10px_rgba(255,0,0,0.8)]" />
            <div className="absolute -top-2 left-1/2 -translate-x-1/2 w-4 h-4 rotate-45 bg-red-600 shadow-md" />
          </div>

          {/* Fade edges for depth effect */}
          <div
            className="absolute inset-0 pointer-events-none"
            style={{
              background:
                "linear-gradient(to bottom, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 15%, rgba(255,255,255,0) 85%, rgba(255,255,255,1) 100%)",
            }}
          />
        </div>

        {/* Winner display */}
        {currentWinnerId && (
          <div className="text-center mt-4">
            <div className="inline-block bg-gradient-to-r from-[#FFD85A] to-[#F7A531] text-[#4E2A0B] font-extrabold py-2 px-6 rounded-full shadow">
              üèÜ Winner:{" "}
              {players.find((p) => p.id === currentWinnerId)?.name || "Unknown"} üèÜ
            </div>
          </div>
        )}
      </div>

      {/* Keyframes */}
      <style jsx>{`
        @keyframes spinReel {
          0% {
            transform: translateY(0);
          }
          100% {
            transform: translateY(-25%);
          }
        }
      `}</style>
    </div>
  );
};

export default SlotMachine;
