"use client";
import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
//import Confetti from "react-confetti";
import { useWindowSize } from "@uidotdev/usehooks";
import { useContinuousGame } from "../../hooks/useContinuousGame";

import WalletBalanceBadge from "@/components/UIComponents/WalletBalanceBadge/WalletBalanceBadge";
import SlotMachine from "./subcomponents/SlotMachine";
import ResultPopup from "./subcomponents/ResultPopup";
import InsufficientPopup from "./subcomponents/InsufficientPopup";
import BetConfirmationPopup from "./subcomponents/BetConfirmationPopup";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Clock, Users } from "lucide-react";
import PastWinners from "@/components/UIComponents/PastWinners/PastWinners";

const ContinuousBettingWheel: React.FC = () => {
  const {
    gameState,
    loading,
    error,
    joinGame,
    userId,
    playerName,
    gameSessionUuid,
  } = useContinuousGame();

  const [walletBalance, setWalletBalance] = useState<number | null>(null);
  const [hasJoined, setHasJoined] = useState(false);
  const [pendingBet, setPendingBet] = useState<number | null>(null);
  const [showBetPopup, setShowBetPopup] = useState(false);
  const [showInsufficient, setShowInsufficient] = useState(false);
  const [showResult, setShowResult] = useState(false);
  const [isWinner, setIsWinner] = useState(false);
  const { width, height } = useWindowSize();

  const coinImgPath = "/images/Coin.png";
  const quickBetAmounts = [1, 5];

  const fetchWalletBalance = async () => {
    if (!userId) return;
    try {
      const response = await axios.get(
        `${process.env.NEXT_PUBLIC_SERVER_BACKEND_URL}/api/getUserWalletBalance`,
        { params: { userUuid: userId } }
      );
      if (response.data.success) setWalletBalance(response.data.balance);
    } catch (err) {
      console.error("Failed to fetch wallet balance", err);
    }
  };

  useEffect(() => {
    fetchWalletBalance();
  }, [userId, gameState]);

  const handleQuickBet = (amount: number) => {
    if (!playerName?.trim()) return alert("Please enter your name first");
    if ((walletBalance ?? 0) < amount) return setShowInsufficient(true);
    setPendingBet(amount);
    setShowBetPopup(true);
  };

  const confirmBet = async () => {
    if (!pendingBet) return;
    setShowBetPopup(false);

    try {
      const releaseResponse = await axios.post(
        `${process.env.NEXT_PUBLIC_SERVER_BACKEND_URL}/api/createUserGame`,
        {
          userUuid: userId,
          gameSessionUuid,
          sessionUuid: gameState?.roundNumber,
          amount: pendingBet,
        }
      );

      if (!releaseResponse.data?.status) {
        alert("Failed to place bet.");
        return;
      }

      const result = await joinGame((playerName ?? "").trim(), pendingBet);
      if (result.success) setHasJoined(true);
    } catch (err) {
      alert("Error joining game.");
    } finally {
      setPendingBet(null);
    }
  };

  useEffect(() => {
    if (gameState?.phase === "finished" && userId) {
      const isUserWinner = gameState.winner?.id === userId;
      setIsWinner(isUserWinner);
      setShowResult(true);
      setTimeout(() => setShowResult(false), 6000);
    }
  }, [gameState?.phase]);

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs < 10 ? "0" : ""}${secs}`;
  };

  const formatNumber = (num: number) =>
    new Intl.NumberFormat("en-IN", { minimumFractionDigits: 0 }).format(num);

  if (loading)
    return (
      <div className="min-h-screen bg-gradient-to-br from-yellow-300 via-yellow-400 to-yellow-500 flex items-center justify-center">
        <p className="text-[#4E2A0B] font-bold text-xl">Loading game...</p>
      </div>
    );

  if (error)
    return (
      <div className="min-h-screen flex items-center justify-center bg-yellow-100">
        <Card className="bg-[#FFF5C3] border-[#F7A531]">
          <CardContent className="text-center">
            <p className="text-red-500 font-bold">{error}</p>
            <Button onClick={() => window.location.reload()} className="mt-2">
              Retry
            </Button>
          </CardContent>
        </Card>
      </div>
    );

  return (
    <div
      className="min-h-screen bg-cover bg-center bg-no-repeat overflow-y-auto transition-opacity duration-700"
      style={{
        backgroundImage: "url('/images/background_image.png')",
        backgroundColor: "#FFD85A",
      }}
    >
      {/* Confetti for winner */}
      {/* {showResult && isWinner && (
        <Confetti width={width ?? 300} height={height ?? 300} numberOfPieces={200} recycle={false} />
      )} */}

      {/* Popups */}
      <ResultPopup
        show={showResult}
        isWinner={isWinner}
        onClose={() => setShowResult(false)}
      />
      <InsufficientPopup
        show={showInsufficient}
        onClose={() => setShowInsufficient(false)}
      />
      <BetConfirmationPopup
        show={showBetPopup}
        amount={pendingBet ?? 0}
        onConfirm={confirmBet}
        onCancel={() => setShowBetPopup(false)}
        isPlacing={false}
      />

      {/* Header Section */}
      <div className="relative w-full flex justify-center items-start pt-8 pb-2">
        {/* Centered Monkey Logo */}
        <img
          src="/images/monkey_logo.png"
          alt="Monkey Banana Logo"
          className="w-[190px] md:w-[230px] h-auto drop-shadow-[0_2px_3px_rgba(0,0,0,0.25)]"
        />

        {/* Wallet on top-right (independent of logo alignment) */}
        <div className="absolute top-6 right-6">
          <WalletBalanceBadge balance={walletBalance ?? 0} />
        </div>
      </div>

      {/* Slot Machine */}
      <div className="mt-6">
        <SlotMachine
          players={gameState?.players || []}
          currentWinnerId={gameState?.winner?.id}
          isSpinning={gameState?.phase === "spinning"}
        />
      </div>

      {/* Status */}
      <div className="text-center mt-4">
        <h2 className="text-[#4E2A0B] font-bold text-lg sm:text-2xl">
          {gameState?.phase === "betting"
            ? "Place your bets!"
            : gameState?.phase === "spinning"
            ? "Spinning the wheel..."
            : gameState?.phase === "finished"
            ? `Winner: ${gameState?.winner?.name}`
            : "Waiting for next round..."}
        </h2>
      </div>

      {/* Timer + Bet Buttons */}
      <div className="px-6 mt-6 mb-10">
        {gameState?.phase === "betting" && (
          <>
            <Card className="bg-[#FFF5C3] border-[#F7A531] mb-4">
              <CardContent className="flex justify-between items-center text-[#4E2A0B] font-bold">
                <div className="flex items-center gap-2">
                  <Clock className="text-[#4E2A0B]" />
                  <span>Next Spin</span>
                </div>
                <span className="text-xl">
                  {formatTime(gameState.timeLeft)}
                </span>
              </CardContent>
            </Card>

            {!hasJoined && (
              <div className="flex justify-center gap-3">
                {quickBetAmounts.map((amount) => (
                  <Button
                    key={amount}
                    onClick={() => handleQuickBet(amount)}
                    className="bg-gradient-to-b from-[#FFD85A] to-[#F7A531] text-[#4E2A0B] font-extrabold px-6 py-2 rounded-full hover:brightness-105"
                  >
                    <img
                      src={coinImgPath}
                      alt="coin"
                      className="w-5 h-5 inline mr-1"
                    />
                    {formatNumber(amount)}
                  </Button>
                ))}
              </div>
            )}

            {hasJoined && (
              <Card className="bg-[#FFF5C3] border-[#F7A531] mt-4 text-center">
                <CardContent>
                  <p className="text-green-700 font-bold">
                    ‚úÖ You‚Äôre in this round!
                  </p>
                </CardContent>
              </Card>
            )}
          </>
        )}

        {gameState?.phase === "finished" && (
          <Card className="bg-[#FFF5C3] border-[#F7A531] mt-4 text-center animate-bounce">
            <CardContent>
              <p className="text-[#4E2A0B] font-extrabold text-lg">
                üéâ {gameState?.winner?.name} won {gameState?.totalPot} coins! üéâ
              </p>
            </CardContent>
          </Card>
        )}
      </div>

      {/* Active Players */}
      {gameState?.players && (
        <div className="bg-[#FFD85A] rounded-t-2xl pt-6 pb-10 px-4">
          <div className="flex items-center justify-center gap-2 mb-4">
            <Users className="text-[#4E2A0B]" />
            <span className="text-[#4E2A0B] font-bold text-lg">
              Active Players
            </span>
          </div>

          <div className="space-y-2 max-h-80 overflow-y-auto scrollbar-hide">
            {gameState.players.map((p) => (
              <Card key={p.id} className="bg-[#FFF5C3] border-[#F7A531]">
                <CardContent className="flex justify-between items-center">
                  <div className="flex items-center gap-2">
                    <img
                      src={p.profileImage || "/images/default-avatar.png"}
                      alt={p.name}
                      className="w-10 h-10 rounded-full"
                    />
                    <p className="text-[#4E2A0B] font-bold">{p.name}</p>
                  </div>
                  <div className="flex items-center gap-1 text-[#4E2A0B] font-bold">
                    <img src={coinImgPath} alt="coin" className="w-5 h-5" />
                    {formatNumber(p.amount)}
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      )}

      {/* Past Winners */}
      <div className="px-4 mt-8">
        <PastWinners refreshTrigger={gameState?.roundNumber || 0} />
      </div>
    </div>
  );
};

export default ContinuousBettingWheel;
